.hide {
  display: none;
}

main {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

#details {
  border: 3px solid var(--foreground);
  width: 50%;
  min-width: 450px;
  max-width: 500px;
  color: var(--foreground);
  text-align: center;
  border-radius: 25px;
  padding: 4%;
  display: block;
}

#details input {
  background-color: var(--foreground);
  border: 0;
  border-radius: 15px;
  width: 60%;
  margin: auto;
  padding: 1%;
  font-size: 1rem;
  display: block;
}

.field {
  margin-top: 4%;
}

#details button {
  background-color: var(--foreground);
  cursor: pointer;
  border: 0;
  border-radius: 15px;
  width: 30%;
  height: 10%;
  margin-top: 5%;
  margin-bottom: 5%;
  font-size: 1rem;
}

#details button:hover {
  background-color: var(--color3);
  color: var(--foreground);
}

#message {
  margin-top: 3%;
}

#details input:focus {
  border: none;
  outline: none;
}

#no-account-prompt {
  font-size: .8rem;
}
